import React from 'react';
import {
  Form,
  Select,
  InputNumber,
  Switch,
  Radio,
  Slider,
  Button,
  Upload,
  Icon,
  Rate,
  Tooltip,
  Input,
  Table,
  Card,
  Divider,
  DatePicker,
  Row,
  Col,
  Modal,
  message,
  Popconfirm,
  Alert,
  BackTop
} from 'antd';
import { ColumnProps, TableRowSelection, TableProps } from 'antd/es/table';
import { SorterResult } from 'antd/es/table';
import { FormComponentProps } from 'antd/es/form';
import Strategy from '../../models/Strategy';
import Global from '../../models/Global';
import {
  StandardTableColumnProps,
  TableListParams,
  TableListData,
  TableListPagination,
  TableListItem
} from '../Table';
import CreateVideo from './CreateVideo';
import { inject, observer } from 'mobx-react';
const FormItem = Form.Item;
const Option = Select.Option;
interface Props {
  form: FormComponentProps['form'];
  strategy: Strategy;
  global: Global;
}
interface State {
  columns: StandardTableColumnProps[];
  loading: boolean;
  visible: boolean;
  currItem: any;
  pagination: TableListPagination;
  stateMaps: any;
  isShow: boolean;
  dataShow: any;
}
@inject('strategy', 'global')
@observer
class VideoStrategy extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      stateMaps: {
        1: '上架',
        2: '下架'
      },
      isShow: false,
      dataShow: {},
      loading: true,
      visible: false,
      currItem: {},
      pagination: {
        total: 20,
        pageSize: 20,
        current: 1
      },
      columns: [
        {
          title: '视频名称',
          dataIndex: 'title'
        },
        {
          title: '视频说明',
          dataIndex: 'content',
          width: 600
        },
        {
          title: '视频',
          dataIndex: 'num',
          render: (text: string, record: any) => (
            <video
              style={{ width: 100 }}
              src={
                record.bmMaterialList.length > 0 && record.bmMaterialList[0].url
              }
              controls
            ></video>
          )
        },
        {
          title: '城市',
          dataIndex: 'city'
        },
        {
          title: '关联旅游产品',
          dataIndex: 'cityInfo',
          render: (text: string, record: any) => (
            <span>
              {record.bmTourProductDaysList[0] &&
                record.bmTourProductDaysList[0].title}
            </span>
          )
        },
        {
          title: '状态',
          dataIndex: 'state',
          render: (text: number, record: any) => (
            <span>{this.state.stateMaps[text]}</span>
          )
        },
        {
          title: '操作',
          dataIndex: 'play',
          render: (text: string, record: any) => (
            <span>
              <a
                href="javacript:void(0);"
                onClick={() => this.CreateHotel(record)}
              >
                编辑
              </a>
              <Divider type="vertical" />
              {/* <Popconfirm
                title="你确定删除吗？"
                icon={<Icon type="question-circle-o" style={{ color: 'red' }} />}
                okText="确认"
                cancelText="取消"
                onConfirm={() => this.DeleteSingle(record)}
              >
                <a href="javacript:void(0);">删除</a>
              </Popconfirm>
              <Divider type="vertical" /> */}
              {record.state === 2 && (
                <span>
                  <Popconfirm
                    title="你确定上架吗？"
                    icon={
                      <Icon type="question-circle-o" style={{ color: 'red' }} />
                    }
                    okText="确认"
                    cancelText="取消"
                    onConfirm={() => this.EnableShutdown(record)}
                  >
                    <a href="javacript:void(0);">上架</a>
                  </Popconfirm>
                  <Divider type="vertical" />
                </span>
              )}
              {record.state === 1 && (
                <span>
                  <Popconfirm
                    title="你确定下架吗？"
                    icon={
                      <Icon type="question-circle-o" style={{ color: 'red' }} />
                    }
                    okText="确认"
                    cancelText="取消"
                    onConfirm={() => this.EnableShutdown(record)}
                  >
                    <a href="javacript:void(0);">下架</a>
                  </Popconfirm>
                  <Divider type="vertical" />
                </span>
              )}
            </span>
          )
        }
      ]
    };
  }
  componentDidMount() {
    this.getVideoStrategyPage();
  }
  getVideoStrategyPage = (params: any = {}) => {
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let payload = {
          ...values
        };
        if (!params.current) {
          params.current = 1;
        }
        // if (params.current === 1) {
        //   params.ts = new Date().valueOf();
        // }
        // else {
        //   params.ts = this.props.message.messagePage.ts;
        // }
        if (!params.page_size) {
          params.size = 20;
        }
        if (payload.timeRange) {
          if (payload.timeRange.length !== 0) {
            payload.start_at = parseInt(payload.timeRange[0].valueOf());
            payload.end_at = parseInt(payload.timeRange[1].valueOf());
          } else {
            payload.start_at = 0;
            payload.end_at = 0;
          }
        }
        payload = {
          ...payload,
          ...params
        };
        this.props.strategy.getVideoStrategyPage({
          data: {
            ...payload
          },

          callback: res => {
            if (res.code === 0) {
              this.setState({
                loading: false
              });
            }
          }
        });
      } else {
        console.log('getVideoStrategyPage error');
      }
    });
  };
  DeleteSingle = (record: { id: string }) => {
    this.props.strategy.DeleteVideoStrategyPage({
      data: {
        id: record.id
      },
      callback: res => {
        if (res.code === 0) {
          message.success(res.msg || '操作成功');
          this.getVideoStrategyPage();
        } else {
          message.error(res.msg || '操作失败');
        }
      }
    });
  };
  EnableShutdown = (record: any) => {
    this.props.strategy.EditVideoUpOrDown({
      data: {
        id: record.id,
        state: record.state === 2 ? 1 : 2
      },
      callback: res => {
        if (res.code === 0) {
          message.success(res.msg || '操作成功');
          this.getVideoStrategyPage();
        } else {
          message.error(res.msg || '操作失败');
        }
      }
    });
  };
  ShowVideo = (item?: any) => {
    this.isShowVideo(true);
    this.setState({
      dataShow: item
    });
  };
  isShowVideo = (bool: boolean) => {
    this.setState({
      isShow: bool
    });
  };
  isCreateHotel = (bool: boolean) => {
    this.setState({
      visible: bool
    });
  };
  CreateHotel = (item?: any) => {
    this.isCreateHotel(true);
    this.setState({
      currItem: item
    });
  };
  PropsInfo = (bool: boolean) => {
    this.isCreateHotel(false);
    this.isShowVideo(false);
  };
  handleTableChange = (
    pagination: Partial<TableListPagination>,
    filtersArg: Record<keyof TableListItem, string[]>,
    sorter: SorterResult<TableListItem>
  ) => {
    const pager = {
      ...this.state.pagination
    };
    pager.current = pagination.current || 1;
    this.setState({
      pagination: pager
    });
    this.getVideoStrategyPage({
      size: pagination.pageSize,
      current: pagination.current
    });
    window.scroll(0, 0);
  };
  handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.getVideoStrategyPage(values);
      }
    });
  };
  render() {
    const info = this.props.strategy.videoStraegyPage;
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 14 }
    };
    const { getFieldDecorator } = this.props.form;
    return (
      <Card title="视频攻略管理" bordered={false} loading={this.state.loading}>
        {this.state.visible && (
          <Modal
            visible={this.state.visible}
            width={1100}
            onCancel={() => this.isCreateHotel(false)}
            footer={null}
          >
            <CreateVideo
              data={this.state.currItem}
              onClose={() => {
                this.PropsInfo(false);
                this.getVideoStrategyPage({
                  size: this.state.pagination.pageSize,
                  current: this.state.pagination.current
                });
              }}
            />
          </Modal>
        )}
        <div className="tableList">
          <BackTop className="ant-back-top-inner" />
          <Form onSubmit={this.handleSubmit}>
            <Row
              gutter={{ md: 8, lg: 24, xl: 48 }}
              style={{ marginTop: '20px' }}
            >
              <Col xl={10} md={24} sm={24}>
                <FormItem
                  label="视频名称"
                  {...formItemLayout}
                  className="form-inline-item"
                >
                  {getFieldDecorator('name')(<Input placeholder="视频名称" />)}
                </FormItem>
              </Col>
              <Col xl={10} md={24} sm={24}>
                <FormItem
                  label="状态"
                  {...formItemLayout}
                  className="form-inline-item"
                >
                  {getFieldDecorator('city')(
                    <Select style={{ width: 200 }} placeholder="请选择状态">
                      <Option key={1} value={1}>
                        上架
                      </Option>
                      <Option key={2} value={2}>
                        下架
                      </Option>
                    </Select>
                  )}
                </FormItem>
              </Col>
              <Col
                xl={10}
                md={24}
                sm={24}
                offset={2}
                style={{ marginBottom: '10px' }}
              >
                <div className="submitButtons">
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="listsearch"
                  >
                    查询
                  </Button>
                </div>
              </Col>
            </Row>
          </Form>
          <Col xl={12} md={24} sm={24} style={{ marginBottom: '10px' }}>
            <Button
              type="primary"
              htmlType="submit"
              className="listsearch"
              onClick={() => {
                this.CreateHotel();
              }}
            >
              添加视频攻略
            </Button>
          </Col>
          <Col span={24}>
            <Table
              columns={this.state.columns}
              rowKey={'id' || 'key'}
              dataSource={info.records}
              pagination={{
                ...this.state.pagination,
                total: info.total,
                current: info.current,
                pageSize: info.size,
                showQuickJumper: true
                // hideOnSinglePage: true
              }}
              onChange={this.handleTableChange}
            />
          </Col>
        </div>
      </Card>
    );
  }
}
export default Form.create<Props>()(VideoStrategy);
